export default {
	data() {
		return {
			isHide: false,
			n: true,
			show: false,
			phone: '',
			password: '',
			shade: false
		};
	},
	onLoad() {

		var that = this;
		uni.getSetting({
			success: function(res) {
				console.log(res)
				if (res.authSetting['scope.userInfo']) {
					//用户已经授权
					uni.getUserInfo({
						success: function(res) {
							// 用户已经授权过,不需要显示授权页面,所以不需要改变 isHide 的值
							// 根据自己的需求有其他操作再补充
							// 我这里实现的是在用户授权成功后，调用微信的 wx.login 接口，从而获取code
							uni.login({
								success: res => {
									// 获取到用户的 code 之后：res.code
									console.log('用户的code:' + res.code);
									// 可以传给后台，再经过解析获取用户的 openid
									uni.request({
									    url:that.url+'users/authorization/',
									    method: 'POST',
									    data: {
									        code: res.code
									    },
									    headers: {
									        'Content-Type': 'application/json'
									    },
									    success: res => {
									        console.log(res);
									        uni.setStorageSync('openid', res.data.data);
									       
									    },
									});
									
								}
							});
						}
					});
				} else {
					// 用户没有授权
					// 改变 isHide 的值，显示授权页面
					that.isHide = true;
				}
			}
		});

	},

	methods: {
		autuWXLogin: function(e) {
			console.log(e)
			if (e.detail.userInfo) {
				//用户按了允许授权按钮
				var that = this;
				// 获取到用户的信息了，打印到控制台上看下
				console.log('用户的信息如下：');
				console.log(e.detail.userInfo);
				//授权成功后,通过改变 isHide 的值，让实现页面显示出来，把授权页面隐藏起来
				that.isHide=false
			} else {
				//用户按了拒绝按钮
				uni.showModal({
					title: '警告',
					content: '您点击了拒绝授权，将无法进入小程序，请授权之后再进入!!!',
					showCancel: false,
					confirmText: '返回授权',
					success: function(res) {
						// 用户没有授权成功，不需要改变 isHide 的值
						if (res.confirm) {
							console.log('用户点击了“返回授权”');
						}
					}
				});
			}
		},
		quickLogin: function() {
			uni.redirectTo({
				url: '../otherLogin/otherLogin'
			});
		},
		forgetPassword: function() {
			uni.navigateTo({
				url: '../getBackPassword/getBackPassword',
				animationType: 'fade-in',
				animationDuration: 200
			});
		},
		next: function() {
			this.n = false;
		},
		back: function() {
			this.n = true;
		},
		cancel: function() {
			this.shade = false;
		},
		getPhoneValue: function(e) {
			this.phone = e.detail.value;
		},
		getPasswordValue: function(e) {
			this.password = e.detail.value;
		},
		sure: function() {
			uni.redirectTo({
				url: '../register/register'
			});
		},
		login() {
			var _self = this;
			if (this.phone == '') {
				uni.showToast({
					icon: 'none',
					title: '请输入手机号'
				});
				return false;
			}
			var myreg = /^(16[0-9]|14[0-9]|13[0-9]|15[0-9]|17[0-9]|18[0-9]|19[0-9])\d{8}$$/;
			if (!myreg.test(this.phone)) {
				uni.showToast({
					title: '请输入正确的手机号',
					icon: 'none',
					mask: true,
					duration: 2000
				});
				return false;
			}
			if (!this.password) {
				uni.showToast({
					title: '请输入密码',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			uni.request({
				url: this.url + 'users/login/',
				method: 'POST',
				data: {
					mobile: this.phone,
					password: this.password
				},
				headers: {
					'Content-Type': 'application/json'
				},
				success: res => {
					uni.setStorageSync('phone', this.phone);
					uni.setStorageSync('token', res.data.token);
					_self.global_.phone = this.phone;
					_self.global_.token = uni.getStorageSync('token');
					if (res.sthatusCode == 401) {
						this.shade = true;
					}
					if (res.statusCode == 402) {
						uni.showToast({
							title: '密码错误',
							icon: 'none'
						});
					}
					if (res.statusCode == 200) {
						uni.reLaunch({
							url: '../index/index'
						});
					}
					if (res.statusCode == 412) {
						uni.showToast({
							title: '请用手机短信快速登录,并设置密码',
							icon: 'none'
						});
					}
				},
				fail: () => {},
				complete: () => {}
			});
		}
	}
};
